/* header styles */

.header-container {
    width: 100vw;
    height: 10vh;
    padding: 30px;
}

#header-icon {
    width: 30px;
    height: 30px;
    color: #34666c;
    margin-left: 5vw;
    margin-bottom: -3px;
}

#header-icon {
    transition: all 0.3s ease-in-out 0s;
}

#header-icon:hover {
    cursor: default;
    transform: rotate(360deg);
    transition: all 0.3s ease-in-out 0s;
}

.header-link {
    font-size: 32px;
    color: #34666c;
    margin: 10px;
    text-decoration: none;
}

.header-link:hover {
    color: #4fa1aa;
}

/* footer styles */
.footer-container {
    padding: 30px;
    width: 100vw;
    height: 10vh;
    background: linear-gradient(90deg, #1495a7 0%, #003687 167%);
}

.footer-link {
    text-decoration: none;
    font-weight: bold;
    font-size: 16px;
    color: white;
    margin-left: 20px;
    margin-right: 20px;
}

/* /home styles */
.home-main-container {
    width: 100vw;
    height: 80vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px;
}

#home-portrait {
    width: 360px;
    margin-left: 10vw;
    height: 360px;
    border-radius: 30px;
}

.home-text-container {
    max-width: 22vw;
    color: black;
}

#home-left-container {
    align-items: left;
}

#home-presentation-header {
    font-size: 36px;
    text-align: start;
}

#home-presentation-content {
    font-size: 22px;
    text-align: start;
}

.home-button-container {
    width: max-content;
    align-items: left;
}

.home-button {
    flex: 1 1 auto;
    font-size: 16px;
    font-weight: bold;
    margin: 10px;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 24px;
    padding-right: 24px;
    text-align: center;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    /* text-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;*/
    border-radius: 10px;
}

.home-button:hover {
    background-position: right center;
    /* change the direction of the change here */
}

#home-contact-button {
    background-image: linear-gradient(to right, #1495a7 0%, #c2e9fb 51%, #5e9aa2 100%);
}

#home-resume-button {
    text-decoration: none;
    color: #1495a7;
    background-color: white;
}